.container-sales {
  width: 100%;
  padding: 0 24px;
  margin: 0 auto;
}

@media screen and (min-width: 320px) {
  .container-sales {
    max-width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container-sales {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container-sales {
    max-width: 1200px;
  }
}

@media screen and (min-width: 1200px) {
  .container-sales--flex-desk {
    display: flex;
  }
}

@media screen and (min-width: 768px) {
  .container-sales--flex-tab {
    display: flex;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

