.sales-section-title {
  text-align: center;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.3;
  margin-bottom: 32px;
  text-transform: uppercase;
  color: #202020;
}

@media screen and (min-width: 768px) {
  .sales-section-title {
    font-size: 34px;
    margin-bottom: 40px;
  }
}

@media screen and (min-width: 1200px) {
  .sales-section-title {
    font-size: 44px;
    margin-bottom: 52px;
  }
}

.sales-section-title--hero {
  font-size: 22px;
  margin: 0;
}

@media screen and (min-width: 768px) {
  .sales-section-title--hero {
    font-size: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .sales-section-title--hero {
    font-size: 48px;
  }
}

.sales-section-title--learn-profitably {
  margin-bottom: 52px;
}

@media screen and (min-width: 768px) {
  .sales-section-title--learn-profitably {
    text-align: start;
  }
}

@media screen and (min-width: 1200px) {
  .sales-section-title--learn-profitably {
    margin-bottom: 93px;
  }
}

.sales-hero_title {
  font-size: 22px;
  font-weight: 700;
  line-height: 1.3;
  text-transform: uppercase;
  color: #202020;
}

@media screen and (min-width: 768px) {
  .sales-hero_title {
    font-size: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .sales-hero_title {
    font-size: 48px;
  }
}

.sales-hero {
  height: 600px;
  background-color: #FED32B;
}

@media screen and (min-width: 768px) {
  .sales-hero {
    height: 640px;
  }
}

.studding-wrapper {
  padding: 52px 0;
  position: relative;
}

.studding-process {
  padding-bottom: 60px;
}

.studding-process_description {
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0px 1px 2px rgba(92, 46, 145, 0.08), 10px 10px 40px rgba(92, 46, 145, 0.24);
}

@media screen and (min-width: 768px) {
  .studding-process_description {
    padding: 40px;
  }
}

@media screen and (min-width: 1200px) {
  .studding-process_description {
    padding: 60px;
  }
}

.studding-process_timeline {
  box-shadow: 0px 1px 2px rgba(92, 46, 145, 0.08), 10px 10px 40px rgba(92, 46, 145, 0.24);
  padding: 20px;
}

@media screen and (min-width: 768px) {
  .studding-process_timeline {
    padding: 40px;
  }
}

.studding-payments_currency-tabs-list {
  display: flex;
  justify-content: center;
  gap: 16px;
}

@media screen and (min-width: 768px) {
  .studding-payments_currency-tabs-list {
    gap: 52px;
  }
}

@media screen and (min-width: 1200px) {
  .studding-payments_currency-tabs-list {
    gap: 70px;
  }
}

.studding-payments_currency-tabs-item {
  flex-basis: calc((100% - 16px) / 2);
  height: 64px;
  border: 1px solid #AA43A2;
  border-radius: 8px;
  color: black;
}

@media screen and (min-width: 768px) {
  .studding-payments_currency-tabs-item {
    flex-basis: calc((100% - 52px) / 3);
  }
}

@media screen and (min-width: 1200px) {
  .studding-payments_currency-tabs-item {
    flex-basis: 230px;
  }
}

.studding-payments_currency-tabs-btn {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: color 750ms, background-color 750ms;
}

.studding-payments_currency-tabs-btn:hover,
.studding-payments_currency-tabs-btn:focus {
  color: #FEC830;
  background-color: #AA43A2;
}

.studding-payments_per-month-list {
  width: 100%;
  display: flex;
  gap: 24px;
  flex-direction: column;
}

@media screen and (min-width: 768px) {
  .studding-payments_per-month-list {
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    gap: 28px;
  }
}

@media screen and (min-width: 1200px) {
  .studding-payments_per-month-list {
    gap: 22px;
    flex-wrap: nowrap;
  }
}

.studding-payments_per-month-item {
  flex-basis: 100%;
}

@media screen and (min-width: 768px) {
  .studding-payments_per-month-item {
    flex-basis: calc((100% - 56px) / 3);
  }
}

@media screen and (min-width: 1200px) {
  .studding-payments_per-month-item {
    flex-basis: calc((100% - 88px) / 5);
  }
}

.studding-payments_quantity-months {
  width: 100%;
  height: 44px;
  background-color: #FED32B;
  position: relative;
  border-radius: 8px;
  margin-bottom: 8px;
}

.studding-payments_quantity-months:before {
  position: absolute;
  content: "";
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 14px;
  height: 12px;
  background: #FED32B;
  clip-path: polygon(50% 100%, 0 0, 100% 0);
}

.studding-payments_box {
  border: 1px solid #FEC830;
  border-radius: 12px;
  padding: 24px 56px 34px 24px;
}

@media screen and (min-width: 768px) {
  .studding-payments_box {
    padding: 24px 24px 34px 24px;
  }
}

.studding-payments_discount {
  border-radius: 50%;
  background-color: #F9F0F8;
  font-size: 24px;
  margin-bottom: 33px;
}

.studding-payments_new-price {
  font-size: 32px;
  margin-bottom: 8px;
}

.studding-payments_previous-price {
  font-size: 24px;
  text-decoration: line-through;
}

.studding-payments_price-list {
  margin-bottom: 46px;
}

.studding-payments_button-pay {
  display: flex;
  background-color: #AA43A2;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  line-height: 2;
  font-size: 12px;
  max-width: 168px;
  color: white;
  height: 38px;
  border: 1px solid #AA43A2;
  position: relative;
  margin-bottom: 52px;
  text-transform: uppercase;
  transition: color 750ms, background-color 750ms, box-shadow 750ms;
}

.studding-payments_button-pay:hover,
.studding-payments_button-pay:focus {
  color: #202020;
  background-color: transparent;
}

.studding-payments_button-pay::after {
  position: absolute;
  content: "";
  bottom: -20px;
  height: 2px;
  width: 100%;
  background-color: #AA43A2;
}

.studding-payments_tab-pay {
  display: flex;
  justify-content: center;
  align-items: center;
}

.studding-process_description > p {
  color: black;
  font-size: 16px;
  line-height: 1.45;
}

@media screen and (min-width: 768px) {
  .studding-process_description > p {
    font-size: 18px;
  }
}

@media screen and (min-width: 1200px) {
  .studding-process_description > p {
    font-size: 20px;
  }
}

.studding-process_description > b {
  color: black;
  font-size: 12px;
  line-height: 1.45;
  font-weight: 700;
}

@media screen and (min-width: 768px) {
  .studding-process_description > b {
    font-size: 17px;
  }
}

.learn-profitably_item > h3 {
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.3;
}

.gift-wrapper {
  background-color: white;
}

.sales-page_title {
  color: #202020;
  font-size: 20px;
  line-height: 1.3;
  font-weight: 700;
}

@media screen and (min-width: 768px) {
  .sales-page_title {
    font-size: 34px;
  }
}